body {
  background: #5f4a8b;
  margin-bottom: 50px;
}

.logo {
  margin: 20px auto 0;
  display: table;
}

.description {
  background: white;
  max-width: 800px;
  margin: 50px auto 0;
  border-radius: 10px;
  padding: 15px 20px;
}

em {
  font-family: monospace;
  font-style: normal;
}

.moonSugar {
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
}

.moonSugar-item {
  position: absolute;
  left: 0;
  top: 0;
  will-change: transform;
  width: 14px;
  height: 14px;
  background: center center/14px 14px no-repeat;
}

.moonSugar-item[data-type="0"] {
  background-image: url("snowflake-0.svg");
}

.moonSugar-item[data-type="1"] {
  background-image: url("snowflake-1.svg");
}

.moonSugar-item[data-type="2"] {
  background-image: url("snowflake-2.svg");
}

.moonSugar-item[data-type="3"] {
  background-image: url("snowflake-3.svg");
}

.moonSugar-item[data-type="4"] {
  background-image: url("snowflake-4.svg");
}



